@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --blue: hsl(246, 80%, 60%);
  --work: hsl(15, 100%, 70%);
  --play: hsl(195, 74%, 62%);
  --study: hsl(348, 100%, 68%);
  --exercise: hsl(145, 58%, 55%);
  --failure-color: #d8534f;
  --social: hsl(264, 64%, 52%);
  --selfcare: hsl(43, 84%, 65%);
  --very-dark-blue: hsl(226, 43%, 10%);
  --dark-blue: hsl(235, 46%, 20%);
  --desaturated-blue: hsl(235, 45%, 61%);
  --pale-blue: hsl(236, 100%, 87%);
}
* {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  margin: 0;
  padding: 0;
}
body {
  background-color: hsl(226, 43%, 10%);
  background-color: var(--very-dark-blue);
}
.App {
  width: 75vw;
  height: 70vh;
  margin: 25vh auto;
}
.profile {
  background-color: hsl(235, 46%, 20%);
  background-color: var(--dark-blue);
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 100%;
  grid-gap: 20px;
  gap: 20px;
}
.profile {
  grid-row: 1 / span 2;
}

.card {
  border-radius: 20px;
  color: #fff;
}
.work {
  background: url(/time-track/static/media/icon-work.a3f124e5.svg) no-repeat right 10px top -12px,
    hsl(15, 100%, 70%);
  background: url(/time-track/static/media/icon-work.a3f124e5.svg) no-repeat right 10px top -12px,
    var(--work);
}
.play {
  background: url(/time-track/static/media/icon-play.e8d4e569.svg) no-repeat right 10px top -12px,
    hsl(195, 74%, 62%);
  background: url(/time-track/static/media/icon-play.e8d4e569.svg) no-repeat right 10px top -12px,
    var(--play);
}
.study {
  background: url(/time-track/static/media/icon-study.1f0a65e4.svg) no-repeat right 10px top -12px,
    hsl(348, 100%, 68%);
  background: url(/time-track/static/media/icon-study.1f0a65e4.svg) no-repeat right 10px top -12px,
    var(--study);
}
.exercise {
  background: url(/time-track/static/media/icon-exercise.3e7b0282.svg) no-repeat right 10px top -2px,
    hsl(145, 58%, 55%);
  background: url(/time-track/static/media/icon-exercise.3e7b0282.svg) no-repeat right 10px top -2px,
    var(--exercise);
}
.social {
  background: url(/time-track/static/media/icon-social.64be0c18.svg) no-repeat right 10px top -12px,
    hsl(264, 64%, 52%);
  background: url(/time-track/static/media/icon-social.64be0c18.svg) no-repeat right 10px top -12px,
    var(--social);
}
.self-care {
  background: url(/time-track/static/media/icon-self-care.a161aff5.svg) no-repeat right 10px top -12px,
    hsl(43, 84%, 65%);
  background: url(/time-track/static/media/icon-self-care.a161aff5.svg) no-repeat right 10px top -12px,
    var(--selfcare);
}
.small-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

@media (max-width: 768px) {
  .grid {
    grid-template-columns: 1fr;
  }
  .small-card {
    height: 30vh;
  }
}

.card-container {
  background-color: var(--dark-blue);
  border-radius: 16px;
  width: 100%;
  height: 80%;
  overflow: hidden;
}
.card-container:hover {
  cursor: pointer;
  background-color: var(--blue);
}
.ellipsisHover {
  background-color: var(--dark-blue);
  border-radius: 16px;
  width: 100%;
  height: 80%;
  overflow: hidden;
}
.card-title {
  font-size: 18px;
}
.card-container .title-container,
.ellipsisHover .title-container {
  margin: 20px;
  display: flex;
  justify-content: space-between;
}
.ellipsis {
  height: 5px;
  width: 20px;
  margin-left: 10px;
}
.ellipsis:hover {
  transform: scale(1.5);
  cursor: pointer;
}
.card-details {
  margin: 20px;
}
.current-time {
  font-size: 40px;
}
.previous-time {
  font-size: 14px;
  color: #988ffb;
  margin: 10px 0;
}
@media (max-width: 768px) {
  .card-details {
    display: flex;
    justify-content: space-between;
  }
}

.profile-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.profile-img {
  width: 75px;
  border: 3px solid #fff;
  border-radius: 50%;
  margin: 30px 20px;
}
.profile-details {
  background-color: var(--blue);
  border-radius: 20px;
  width: 100%;
  height: 10%;
}
.report-text {
  color: #988ffb;
  margin-left: 20px;
  font-size: 0.8rem;
}
.profile-name {
  color: #fff;
  font-size: 1.5rem;
  margin: 10px 20px 50px 20px;
}
.profile-options {
  margin: 20px 0px;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.profile-options li {
  margin: 10px 20px;
  padding: 0 10px;
  cursor: pointer;
}
.profile-options .active{
  color: #fff;
}
.profile-options .inactive{
  color: var(--blue);
}
.profile-options li:hover{
  transform: scale(1.05);
  color: #fff;
}

@media (max-width: 768px) {
  .profile-options {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .profile-details {
    display: flex;
    justify-content: center;
  }
  .profile-img {
    width: auto * 0.5;
    height: auto * 0.5;
  }
  .profile-content {
    align-self: flex-end;
  }
}
@media (max-width: 500px) {
  .report-text {
    font-size: 3vw;
  }
  .profile-name {
    font-size: 4vw;
  }
} ;

